import React from "react";
import ReactDOM from "react-dom";
import MuiThemeProvider from "material-ui/styles/MuiThemeProvider";
import Divider from "material-ui/Divider";
import Paper from "material-ui/Paper";
import TextField from "material-ui/TextField";
import AppBar from "material-ui/AppBar";
import "./index.css";

const TopBar = () => (
  <AppBar
    title={
      <span>Tabi-Tools // Enter info and copy the text for train routes</span>
    }
  />
);
class MapRoute extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      firstLine: "",
      firstStation: "",
      endStation: "",
      cost: "",
      time: ""
    };

    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(event) {
    this.setState({ [event.target.name]: event.target.value });
  }

  render() {
    return (
      <div>
        <Paper zDepth={2}>
          <TextField
            name="firstStation"
            hintText="First Station"
            underlineShow={false}
            value={this.state.firstStation}
            onChange={this.handleChange}
          />
          <Divider />

          <TextField
            name="endStation"
            hintText="End Station"
            underlineShow={false}
            value={this.state.endStation}
            onChange={this.handleChange}
          />
          <Divider />
          <TextField
            name="firstLine"
            hintText="First Train Line"
            underlineShow={false}
            value={this.state.firstLine}
            onChange={this.handleChange}
          />
          <Divider />
          <TextField
            name="time"
            hintText="Time"
            underlineShow={false}
            value={this.state.time}
            onChange={this.handleChange}
          />
          <Divider />
          <TextField
            name="cost"
            hintText="Cost"
            underlineShow={false}
            value={this.state.cost}
            onChange={this.handleChange}
          />
          <Divider />
        </Paper>
        <Paper zDepth={2}>
          <h1>
            ◆{this.state.firstStation} → {this.state.endStation}
          </h1>
          <p />
          <h1>Train: {this.state.firstLine}</h1>
          <h1>Time: {this.state.time} Mins</h1>
          <h1>Fare: {this.state.cost} Yen</h1>
          <p />
          <h1>Thank you for using Tabiko.</h1>
        </Paper>
      </div>
    );
  }
}

class MainUI extends React.Component {
  render() {
    return (
      <div className="container">
        <MuiThemeProvider>
          <TopBar />
        </MuiThemeProvider>
        <MuiThemeProvider>
          <MapRoute />
        </MuiThemeProvider>
      </div>
    );
  }
}

ReactDOM.render(
  <div>
    <MainUI />
  </div>,
  document.getElementById("root")
);



